<template>
    <div>
        <div id="container" ref="amap"></div>
        <div class="search-box">
            <img src="../assets/image/logo.png" alt="" class="logo">
            <el-input id="tipinput" v-model="locationValue" type="text" placeholder="搜索位置、关键词、公交站">
                <el-select v-model="select" slot="prepend" placeholder="请选择">
                    <el-option label="周边检索" value="1"></el-option>
                    <el-option label="路线检索" value="2"></el-option>
                </el-select>
                <el-button slot="append" icon="el-icon-search" @click="searchAddress"></el-button>
            </el-input>
        </div>

        <ul class="address-list" @click="rimSearch">
            <li v-for="item in rimData" :key="`timr${item}`" :class="checkedRim == item ? 'active' : ''">{{ item }}</li>
        </ul>
    </div>
</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader';
export default {
    name: "MapPage",
    data() {
        return {
            select: "1",
            locationValue: "",
            latitude: null,
            longitude: null,
            errorMessage: null,
            auto: null,
            placeSearch: null,
            address: null,
            map: null,
            driving: null,
            lnglat: [],
            // 初始化中心点
            centerLen: [120.001245, 30.279954],
            markers: [],
            position: {},
            route: null,
            checkedRim: '',
            rimData: ["咖啡", "网吧", "棋牌", "台球"],
            dataForm: {
                kqName: undefined,
                kqLocation: undefined,
                kqLongitude: undefined,
                kqLatitude: undefined,
                kqWorkUnit: undefined,
                cronkqAccredit: [],
                kqValidCardRange: undefined,
            },
            rules: {},
        };
    },
    mounted() {
        navigator.geolocation.getCurrentPosition((position) => {
            this.latitude = position.coords.latitude;
            this.longitude = position.coords.longitude;
        })
        this.initAMap();
    },
    methods: {
        initAMap() {
            // let centerLen = [116.397428, 39.90923]
            AMapLoader.load({
                key: 'f454116e1710654af668f62b7d4e5203', // 申请好的key
                version: '2.0', // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15          //plugins: []  // 需要使用的的插件列表，如比例尺'AMap.Scale'等，如果是地图控件，必须再 map.addControl添加  
                plugins: ["AMap.AutoComplete", "AMap.PlaceSearch", "AMap.Geocoder", 'Amap.Driving', "AMap.Riding"]
            })
                .then((AMap) => {
                    this.map = new AMap.Map('container', {
                        // 设置地图容器id  
                        //viewMode: '3D', // 默认2d地图模式  
                        zoom: 12, // 初始化地图级别  
                        zooms: [5, 30], // 地图缩放范围  
                        // 可以设置初始化当前位置  
                        center: this.centerLen // 初始化地图位置  
                    })

                    this.route = new AMap.Riding({
                        map: this.map
                    })



                    AMap.plugin(
                        [
                            'AMap.ElasticMarker',
                            'AMap.Scale',
                            'AMap.ToolBar',
                            'AMap.HawkEye',
                            'AMap.MapType',
                            'AMap.Geolocation',
                        ],
                        () => {
                            this.map.addControl(new AMap.ElasticMarker()) //addControl为地图添加对应的控件
                            this.map.addControl(new AMap.Scale())
                            this.map.addControl(new AMap.ToolBar())
                            this.map.addControl(new AMap.HawkEye())
                            this.map.addControl(new AMap.MapType())
                            this.map.addControl(new AMap.Geolocation())
                        }
                    )

                    // 初始位置添加标记点
                    this.setMarker(this.centerLen)
                    // 点击事件
                    this.map.on('click', this.clickMapHandler)
                    // 关键字查询
                    this.searchMap();

                })
        },
        /**
         * 路线规划
         */
        // toDriving() {
        //     const driving = new AMap.Driving({
        //         map: this.map,
        //         panel: 'panel'
        //     })
        //     this.driving = driving
        //     this.driving.search(
        //         new AMap.LngLat(121.378945, 31.264033),
        //         new AMap.LngLat(121.504128, 31.318716),
        //         function (status, result) {
        //             // result 即是对应的驾车导航信息，相关数据结构文档请参考  https://lbs.amap.com/api/javascript-api/reference/route-search#m_DrivingResult
        //             if (status === 'complete') {
        //                 console.log('绘制驾车路线完成', result)
        //             } else {
        //                 console.log('获取驾车数据失败：' + result)
        //             }
        //         }
        //     )
        // },

        /**
         *  初始化查询
         */
        async searchMap() {
            const address = await this.getAddressInfo(this.centerLen)
            console.log(address);
            const asd = await this.getAddressAsync(this.lnglat)
            console.log(asd);
            this.auto = new AMap.AutoComplete({
                input: "tipinput", // 使用联想输入的input的id
                city: address.regeocode.addressComponent.city,  //限制检索范围
            })
            //构造地点查询类
            this.placeSearch = new AMap.PlaceSearch({
                map: this.map,
            });
            // 当选中某条搜索记录时触发
            this.auto.on("select", this.selectSite);
        },

        /**
         * 点击模糊检索下拉框的内容进行检索
         * 自动区分准确地点检索或者周边检索
         * @param e 
         */
        async selectSite(e) {
            if (e.poi.location) {
                this.lnglat = [e.poi.location.lng, e.poi.location.lat];
                this.placeSearch.setCity(e.poi.adcode);
                this.placeSearch.search(e.poi.name); //关键字查询
                const res = await this.getAddressInfo(this.lnglat)
                this.route.search(this.centerLen, this.lnglat, (status, result) => {
                    console.log(status);
                    console.log(result);
                    // result为路线规划的结果
                    // if (status === 'complete') {
                    //     this.map.setFitView(); // 视图自适应
                    // }
                });
            } else {
                this.placeSearch.searchNearBy(e.poi.name, this.centerLen, 500)
                // this.$message.error("查询地址失败，请重新输入地址");
            }
        },

        /**
         * 将点击位置转换成详细地址
         * @param e 
         */
        async clickMapHandler(e) {
            this.dataForm.kqLongitude = e.lnglat.getLng();
            this.dataForm.kqLatitude = e.lnglat.getLat();
            this.lnglat = [e.lnglat.getLng(), e.lnglat.getLat()];

            // let that = this
            // // 点击地图上的位置，根据经纬度转换成详细地址
            // let geocoder = new AMap.Geocoder({});
            // geocoder.getAddress(this.lnglat, function (status, result) {
            //     if (status === 'complete' && result.info === "OK") {
            //         that.dataForm.kqLocation = result.regeocode.formattedAddress;
            //     }
            // });
            const address = await this.getAddressInfo(this.lnglat)
            this.dataForm.kqLocation = address.formattedAddress
            this.position = {
                longitude: e.lnglat.getLng(),
                latitude: e.lnglat.getLat(),
            };
            this.locationValue = this.dataForm.kqLocation //把查询到的地址赋值到输入框
            this.setMarker(this.lnglat, this.dataForm.kqLocation);
            // 点击后自动定位到当前点击位置
            this.map.setCenter(e.lnglat)
        },


        /**
         * 将经纬度转换成详细的地址信息
         * 
         * @param lnglat 
         */
        getAddressAsync(lnglat) {
            let geocoder = new AMap.Geocoder({});
            return new Promise((resolve, reject) => {
                geocoder.getAddress(lnglat, function (status, result) {
                    resolve(result);
                });
            });
        },

        /**
         * geocoder.getAddress 本身是异步执行，
         * 这里将其改成同步执行
         * 
         * @param lnglat 
         */
        async getAddressInfo(lnglat) {
            const address = await this.getAddressAsync(lnglat);
            return address
        },


        /**
         * 将地址转换成经纬度
         * @param address 
         */
        getLocationAsync(address) {
            let geocoder = new AMap.Geocoder({});
            return new Promise((resolve, reject) => {
                geocoder.getLocation(address, (status, result) => {
                    if (status === 'complete' && result.info === 'OK') {
                        return resolve(result)
                    }
                })
            })
        },

        /**
         * 对输入框的检索词进行检索
         * 
         * @searchNearBy  周边检索，可以限制检索范围
         * @search  直接大范围检索，最多限制到城市区域内
         * 
         */
        async searchAddress() {
            if (this.select == 1) {
                this.placeSearch.searchNearBy(this.locationValue, this.centerLen, 500, (status, result) => {
                    console.log(result);
                })
            } else {
                const location = await this.getLocationAsync(this.locationValue)
                const centerInt = [location.geocodes[0].location.lng, location.geocodes[0].location.lat]
                this.placeSearch.search(this.locationValue)
                this.route.search(this.centerLen, centerInt)
            }
        },
        /***
         * 周边检索
         */
        rimSearch(e) {
            this.checkedRim = e.target.innerText
            this.placeSearch.searchNearBy(e.target.innerText, this.centerLen, 500)
        },

        /**
         * 添加标记点
         * @param lnglat 
         */
        setMarker(lnglat, content) {
            this.removeMarker();
            // 自定义标记点
            let marker = new AMap.Marker({
                position: lnglat,
            });
            marker.on('click', (e) => {
                const lnglat = [e.lnglat.lng, e.lnglat.lat]
                let that = this
                let geocoder = new AMap.Geocoder({});
                geocoder.getAddress(lnglat, function (status, result) {
                    if (status === 'complete' && result.info === "OK") {
                        // 自定义弹窗
                        const infoWindow = new AMap.InfoWindow({
                            content: `<div style="width:200px;background-color:#fff;">${result.regeocode.formattedAddress}</div>`, // 信息窗口内容
                            size: new AMap.Size(100, 0), // 信息窗口大小
                            offset: new AMap.Pixel(0, -30) // 信息窗口偏移位置
                        })
                        infoWindow.open(that.map, marker.getPosition());
                    }
                });
            })
            marker.setMap(this.map);
            this.markers.push(marker);
        },
        // 删除之前后的标记点
        removeMarker() {
            if (this.markers) {
                this.map.remove(this.markers);
            }

        }
    }
};  
</script>

<style scoped>
#container {
    padding: 0px;
    margin: 0px;
    width: 100vw;
    height: 100vh;
    position: relative;
}

.search-box {
    position: absolute;
    top: 20px;
    left: 20px;
}

.logo {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 10px;
    width: 30px;
}

#tipinput {
    width: 360px;
    height: 45px;
    padding: 0 20px 0 60px;
    box-sizing: border-box;
    background: #fff;
    border-radius: 3px;
    outline: none;
    border: 1px solid #fcfcfc;
}

.search-content {
    width: 40px;
    height: 45px;
    background: #fff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-content img {
    width: 20px;
    cursor: pointer;
}

.address-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;
    position: absolute;
    top: 20px;
    left: 400px;
    border-left: 1px solid #d2d0d0;
    border-top: 1px solid #d2d0d0;
    border-radius: 4px;
}

.address-list li {
    padding: 10px 20px;
    border-right: 1px solid #d2d0d0;
    border-bottom: 1px solid #d2d0d0;
    color: #666;
    cursor: pointer;
}

.active {
    background-color: #409EFF;
    color: #fff !important;
}

::v-deep .el-input-group__prepend {
    width: 80px !important;
}
</style>
